<template>
  <div class="table-r">
    <div class="box" style="margin-bottom: 0">
      <div class="box-header">
        <h3 class="box-title"><i class="fa fa-user-md"></i> 博主简介</h3></div>
      <div class="box-body no-padding">
        <div class="topauthor">
          <img class="pull-left avatar-photo"
               v-lazy="require('@static/uploads/avatar/header.jpg')" width="100" height="100">

          <a href="javascript:void(0)" class="tag">博主：</a>
          <a href="javascript:void(0)" >{{web_config['web_author']}}</a>
          <div class="talk">
            {{web_config['web_talk']}}
          </div>

          <div class="butauthor">
            <span class="pn pull-left">{{$t('layout.browse')}} {{web_config['web_views']}} 次</span>
            <span class="pull-right">{{$t('layout.comments')}} {{web_config['comments']}} 条</span>
          </div>
        </div>
      </div>

    </div>

    <div class="box">
      <div class="box-header"><h3 class="box-title">
        <i class="fa fa-fire"></i> {{$t('layout.hot_archive')}}</h3></div>
      <div class="box-body no-padding">
        <ul class="nav nav-pills nav-stacked">
          <li v-for="(vol, index) in hot_archive">
            <a href="javascript:void(0)" @click="jumpArchive(vol)">
              <span :class="vol.icon">{{index+1}}</span> {{vol.title}}
            </a>
          </li>
        </ul>
      </div>
    </div>

    <div class="box">
      <div class="box-header"><h3 class="box-title">
        <i class="fa fa-list"></i> {{$t('layout.hot_arctype')}}</h3></div>
      <div class="box-body no-padding">
        <ul class="nav nav-pills nav-stacked">
          <li v-for="vol in hot_arctype">
            <a href="javascript:void(0)" @click="jumpArctype(vol)">
              <span class="label label-info pull-right">{{vol['archive_num']}}</span>
              <i class="fa fa-circle-o"></i> {{vol.typename}}
            </a>
          </li>

        </ul>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
      name: "right",
      data(){
        return {
          hot_archive:[],
          hot_arctype: [],
          hot_archive_icon:[
            'label bg-red',
            'label bg-orange',
            'label bg-green',
            'label bg-aqua',
            'label bg-teal'
          ]
        }
      },
      methods:{
        jumpArchive(article){
          this.$store.commit('front/SET_TYPE_ID', article.type_id)
          this.$router.push({
            name: 'detail_page',
            params: {id: article.id}
          })
        },
        jumpArctype(arctype){

          this.$store.commit('front/SET_TYPE_ID', arctype.id)
          if (arctype.model_id === 2){
            this.$router.push({
              name: 'detail_page',
              params: {id: arctype.id},
              query: {mid: arctype.model_id}
            })
          } else {
            this.$router.push({
              name: 'front_archive_list',
              params:{
                cname:arctype['dirs'] || arctype['typename'],
              }
            })
          }
        },
      },
      computed:{
        web_config(){
          return this.$store.state.front.web_config
        }
      },
      mounted(){
        const hot_archive_icon = this.hot_archive_icon
        this.$store.dispatch('front/fetchHot').then( resp =>{
          if (resp){
            resp.data['hot_archive'].forEach((vol, index) =>{
              this.hot_archive.push({
                icon: hot_archive_icon[index],
                id: vol['id'],
                type_id: vol['type_id'],
                title: vol['title']
              })
            })
            resp.data['hot_arctype'].forEach((vol) =>{
              this.hot_arctype.push({
                typename: vol['typename'],
                id: vol['id'],
                dirs: vol['dirs'],
                model_id: vol['model_id'],
                archive_num: vol['archive_num']
              })
            })
          }
        })
      }
    }
</script>
<style>
  .box-body > .comment {
    margin-left: 18px
  }
  .box-body >.comment>.text-content{
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    width: 190px;
  }
  .topauthor{
    margin-top: 10px;
    font-size: 1em;
  }
  .topauthor > .avatar-photo{
    margin: -5px 15px;
    border: 1px solid #CCCCCC;
  }
  .topauthor .talk{
    padding-top: 8px;
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    width: 160px;
  }
  .topauthor .tag{
    padding: 5px ;
    background: #0090ce;
    color: #fff;
    margin-right: 6px;
    cursor: pointer;
    font-weight: bold;
  }
  .butauthor{
    margin-top: 20px;
    color: #999;
    text-align: center;
    background: #f8f8f8;
    float: left;
    font-size: 14px;
    width: 100%;
    padding: 0 10px;
    border-top: 1px solid #ddd;
  }
  .butauthor > span{
    color: #666;
    float: left;
    width: 49%;
    padding: 9px 0 11px 0;
    display: block;
    cursor: pointer;
  }
  .butauthor > .pn{
    border-right: 1px solid #DDD;
  }
</style>



