<template>
  <footer id="footer" class="app-footer">
    <div class="pull-right hidden-xs"></div>
    <span v-html="web_config['web_footer']"></span>

  </footer>
</template>

<script>
    export default {
      name: "layout_footer",
      computed:{
        web_config(){
          return this.$store.state.front.web_config
        }
      }
    }
</script>



