<template>
  <li :class="{'treeview':isChildren}" @click.stop="toggle">

    <template v-if="isChildren">
      <a href="javascript:void(0);" v-html="parentHtml(model.icon, model.typename)" ></a>
    </template>
    <template v-else>
      <a href="javascript:void (0)" @click="jumplink" v-html="childHtml(model.icon, model.typename)"></a>
    </template>

    <ul class="treeview-menu" v-if="isChildren" ref="child_ul">
      <layout-tree-meun
        v-for="(vol, index) in model.children"
        :key="index"
        :model="vol">
      </layout-tree-meun>
    </ul>
  </li>
</template>

<script>

  export default {
    name:'layoutTreeMeun',
    props: {
      model:Object
    },
    methods:{
      parentHtml(icon, typename){
        return icon +` <span>`+typename+`</span>
        <span class="pull-right-container"><i class="fa fa-angle-left pull-right"></i></span>`
      },

      childHtml(icon, typename){
        return icon + ' '+ typename
      },
      jumplink(){
        const arctype = this.model
        this.$store.commit('front/SET_TYPE_ID', arctype.id)
        if (arctype.model_id === 2){
          this.$router.push({
            name: 'detail_page',
            params: {id: arctype.id},
            query: {mid: arctype.model_id}
          })
        } else {
          this.$router.push({
            name: 'front_archive_list',
            params:{
              cname:arctype['dirs'] || arctype['typename'],
            }
          })
        }
      },
      toggle(){
        if (this.isChildren) {
          const ul = this.$refs.child_ul
          const _ul = $(ul)
          $('.treeview-menu').not(_ul).slideUp()
          $(ul).slideToggle()
        }
      }
    },
    computed:{
      isChildren:function () {
        if (!this.model.hasOwnProperty('children')){
          return false
        }
        const len = this.model.children.length
        return len !== 0
      },
      getMenuName:function () {
        if (this.model.hasOwnProperty('typename')){
          return this.model.typename
        }
      }
    },
  }
</script>



