<template>
  <div class="app-content">
    <div class="table-container">
      <div class="table-l">
        <div class="top-notice clearfix">
          <a class="pull-right" target="_blank"
             :href="web_config['git_fork_url']">
            <img :src="web_config['git_fork_img']" alt="fork"></a>
          <a class="pull-right" target="_blank" :href="web_config['git_star_url']">
            <img :src="web_config['git_star_img']" alt="star"></a>
          <p>{{web_config['web_title'] || $t('layout.title')}}</p>
        </div>
        <div v-if="!isIndex" class="bg-white position">
          <a href="javascript:void(0)" @click="jumpindex" ><i class="fa fa-home"></i> <b>首页</b></a>
          <template v-for="vol in navs">
            <span>&gt;</span>
            <a href="javascript:void(0)" @click="jumplink(vol)"><b>{{vol.typename}}</b></a>
          </template>
        </div>
        <transition name="slide-fade" mode="out-in">
          <router-view />
        </transition>
      </div>
      <right />
    </div>

  </div>
</template>

<script>
    import Right from "../table/right"
    export default {
      name: "layout_content",
      components: { Right },
      computed:{
        navs(){
          return this.$store.state.front.navbar
        },
        isIndex(){
          return this.$route.name === 'front_index'
        },
        web_config(){
          return this.$store.state.front.web_config
        }
      },
      watch:{
        '$route': 'fetchNavs'
      },
      mounted(){
        this.fetchNavs()
      },
      methods:{
        fetchNavs(){
          this.$store.dispatch('front/fetchNavs')
        },
        jumpindex(){
          this.$store.commit('front/SET_TYPE_ID', 0)
          this.$router.push({
            name: 'front_index'
          })
        },

        jumplink(arctype){
          this.$store.commit('front/SET_TYPE_ID', arctype.id)
          this.$router.push({
            name: 'front_archive_list',
            params:{
              cname:arctype['dirs'] || arctype['typename'],
            }
          })
        },
      }
    }
</script>
<style>
  /* 可以设置不同的进入和离开动画 */
  /* 设置持续时间和动画函数 */
  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



