<template>
  <div id="app-box" class="app app-header-fixed app-aside-fixed container app-skin-dark-header">
    <layout-head />
    <layout-sidebar />
    <layout-content />
    <layout-footer />
  </div>
</template>

<script>
  import '@static/global/bootstrap/bootstrap.min'
  import {LayoutHead, LayoutSidebar, LayoutFooter, LayoutContent} from "./components"
  // import Aplayer from 'vue-aplayer'

  export default {
    components: {LayoutFooter, LayoutContent, LayoutSidebar, LayoutHead},
    mounted(){
      this.$store.dispatch('front/fetchWebConfig')
    },
    beforeCreate:function () {
      try{
        document.body.className = 'hold-transition'
      }catch (e) {}
    },
    beforeDestroy:function () {
      try {
        document.body.classList.remove('hold-transition')
      }catch (e) {}
    }
  }
</script>
<style>
  @import "../../../static/system/dist/css/style.css";
  @import "../../../static/system/dist/css/skins/skin.css";
  body {
    background-image: url("../../assets/body-bg.png");
  }

</style>



