<template>
  <aside id="aside" class="app-aside hidden-xs">
    <div class="aside-wrap">
      <div class="app-user-panel">
        <div class="image">
          <img v-lazy="web_avatar" class="img-circle">
        </div>
        <div class="info">{{web_config['web_author'] || $t('layout.author')}}</div>
      </div>

      <ul class="sidebar-menu app-sidebar-menu">
        <li>
          <a href="javascript:void (0)" @click="jumplink">
            <i class="fa fa-home"></i> <span>{{$t('layout.index')}}</span></a>
        </li>
        <tree-menu v-for="(model, index) in list"
                   :model="model"
                   :key="index"/>
      </ul>
    </div>
  </aside>
</template>

<script>
    import TreeMenu from "./layoutTreeMeun"
    export default {
      name: "layout_sidebar",
      components: {TreeMenu},
      computed:{
        web_avatar(){
          return this.$store.state.front.web_avatar
        },
        list(){
          return this.$store.state.front.sidebar
        },
        web_config() {
          return this.$store.state.front.web_config
        }
      },
      mounted(){
        this.$store.dispatch('front/fetchSidebar')
      },
      methods:{
        jumplink(){
          this.$store.commit('front/SET_TYPE_ID', 0)
          this.$router.push({
            name: 'front_index'
          })
        },
      }
    }
</script>



