<template>
  <header id="header" class="app-header navbar" style="z-index: 160;">
    <div class="navbar-header">
      <a href="/" class="logo">
        <span class="xs-menu-toggle pull-right"><i class="fa fa-gear"></i></span>
        <span class="xs-aside-toggle pull-right"><i class="fa fa-bars"></i></span>
        <i class="fa fa-home"></i>
        <span class="folded-hidden">{{author}}</span>
      </a>
    </div>
    <div class="collapse navbar-collapse box-shadow">
      <div class="row">
        <a class="aside-toggle" href="javascript:void(0);"><i class="fa fa-bars"></i></a>
      </div>
    </div>
  </header>
</template>

<script>
    export default {
      name: "layoutHead",
      computed:{
        author(){
          return this.$store.state.front.web_config['author'] || this.$t('layout.author')
        }
      },
      created(){
        const body = $('body')
        body.off('click', '.xs-aside-toggle')
          .on("click", '.xs-aside-toggle', function(event){
          $('#aside').toggleClass('off-screen');
          return false;
        });
        body.off('click', '.aside-toggle');
        body.on("click", '.aside-toggle', function(event){
          $('#app-box').toggleClass('app-aside-folded');
        });
      }
    }
</script>



